$prim_color: #06623b;
$sec-color: #649d66;
$ter-color: #f6d743;
$quat-color: #f6f578;


body {
  background-color: $sec-color !important;
}

.App {
  text-align: center;
}

.cover {
  width: 100vw;
  height: 100vh;
}

.container {
  background-color: $prim_color;
  color: $quat-color;
  max-width: unset !important;
  padding: 2rem;
}

//Home Page

.home-container {
  display: flex;
  flex-wrap: wrap;
}

.home-card {
  padding: 10px;
  margin: 15px 0;
  height: 300px;
  display: flex;
  flex-direction: column;
  background-color: $sec-color !important;

  .home-card-title {
    height: 100px;
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .card-body {
    flex: 4;

    .lazy-load-image-background {
      height: 100%;
      width: 100%;
  
      img {
        height: 100%;
      }
    }
  }
  
  
  .home-card-image {
    height: 200px;
    width: 100%;
    margin: auto;
    object-fit: cover;
  
  }
}

//Detail page

.detail-header {
  padding: 30px;
  font-weight: bold;
  font-size: 30px;
  text-transform: capitalize;
}

.card-row {
  justify-content: center;
  margin: auto;
  padding: 1rem;
}

//Carousel

.detail-page-carousel {
  height: 300px;
  width: 70%;
  margin: auto;

  .image-container {
    height: 300px;
  }

  .card-image {
    height: 80%;
    margin: auto;
    width: 90%;
  }
}

.slick-arrow:before {
  color: $quat-color;
}

//Image container

.image-desc {
  padding: 15px 0;
  font-size: 14;
  color: $ter-color;
}

.column {
  padding: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.image-container {
  margin: auto;
  position: relative;
  
  &.edit {
    width: 30%;

    a {
      position: absolute;
      color:#df4759 !important;
      top: 0;
      right: 0;
      cursor: pointer;
    }

    .card-image {
      width: 100%;
      height: unset;
    }
  }
}



.card-image {
  height: 300px;
  object-fit: cover;
  flex: 1;
}

//Login 
.login-card {
  box-sizing: border-box;
  max-width: 410px;
  margin: 0 auto;
  padding: 0.2rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 3.5rem;

  .login-form {

    width: 100%;
    display: flex;
    flex-direction: column;

    input {
      padding: 1rem;
      border: 1px solid #999;
      margin-bottom: 1rem;
      font-size: 0.8rem;
    }

    button {
      border-color: $sec-color;
      padding: 1rem;
      color: $quat-color;
      font-weight: bold;
      width: 100%;
      margin-bottom: 1rem;
    }
  }
}

//Edit page

.edit-header {
  padding: 30;
  color: $ter-color;
  font-weight: bold;
  font-style: italic;
}

.table-row {
  display: flex;
  flex-direction: row;
  margin: auto;
  padding: 20px;

  .right-col {
    text-align: left;    
    display: block;
    flex: 1;
  }
  
  .left-col {
    text-align: right;
    display: block;
  }
}

@media screen and (min-width: 768px){
  .table-row {
    width: 80%;

    .left-col {
      padding-right: 25px;
      width: 120px;
    }
  }
}

@media screen and (max-width: 767px){
  .table-row {
    padding-left: 0;

    .left-col {
      width: 110px;
      padding-right: 15px;
    }
  }
}

#imageUpload {
  width: 30%;
  margin: 2rem;
}

.edit-buttons{

  margin-top: 1rem;

  button {
    margin: 0 4rem;
    width: 150px;
    padding: 12px;
  }
}

//Header 

.navbar-nav {
  width: 100%;
  position: relative;
}

.navbar-toggler-icon {
  background: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' width='30' height='30' viewBox='0 0 30 30'%3e%3cpath stroke='rgba(246, 245, 120, 1)' stroke-linecap='round' stroke-miterlimit='10' stroke-width='2' d='M4 7h22M4 15h22M4 23h22'/%3e%3c/svg%3e") !important;
}

.navbar-toggler, .navbar-toggler:focus {
    border: none !important; 
    outline: none !important;
}

  .link {
    color: $quat-color !important;
    font-weight: bold;

    &.edit {
      background-color: #28a745;
      width: 150px;
      margin: auto;
      margin-top: 1rem;
    }
  }


.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.link:hover {
  cursor: pointer;
  opacity: 0.75;
}

@media screen and (min-width: 768px){
  .create-new {
    position: absolute;
    right: 0;
  }
}

@media screen and (max-width: 768px){
  .navbar-nav{
    .link.nav-link {
      text-align: right;
    }
  }
}



//Footer 

.footer {
  background-color: #28a745;
  color: $quat-color;
  position: fixed;
  bottom: 0;
  width: 100%;
}


//Loading screen

.loading-row {
  height: 100vh;
  background-color: $sec-color;

  .loading-container {
    color: $quat-color;
    margin: auto;
    margin-top: 15%;
    font-weight: bold;
    font-size: 24;
  }
}


//Kata Pengantar

.pengantar {
  background-color: $sec-color;
  height: '100vh';
  color: $quat-color;
  text-align: left;
  padding: 2rem;

  h1, h2 {
    text-align: center;
    padding: 0.5rem
  }

  li {
    padding: 0.2rem
  }
  
  p {
    padding: 0.75rem 4rem;
    line-height: 1.8;
  }
}



